.directory {
    font:"Segoe UI Variable", "Segoe UI", sans-serif;
    font-size: 14px;
    user-select: none;
    padding: 20px;
  }
  
  .directory .tree,
  .directory .tree-node,
  .directory .tree-node-group {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .directory .tree-branch-wrapper,
  .directory .tree-node__leaf {
    outline: none;
    outline: none;
  }
  
  .directory .tree-node {
    cursor: pointer;
  }
  
  .directory .tree-node:hover {
    background: rgba(255, 255, 255, 0.1);
  }
  
  .directory .tree .tree-node--focused {
    background: rgba(255, 255, 255, 0.2);
  }
  
  .directory .tree .tree-node--selected {
    background: rgb(203,232,254);
  }
  
  .directory .tree-node__branch {
    display: block;
    margin-bottom: 15px;
  }
  
  .directory .icon {
    vertical-align: middle;
    padding-right: 5px;
    zoom: 150%;
    
  }
.text{
    display: inline;
    width: 500px;
}