
.header-detail {
    background: linear-gradient(0deg, #81b0ee, #d5e4f5);
    font-family: "Segoe UI Variable", "Segoe UI", sans-serif;
    color: hsl(207, 100%, 30%);
    font-weight: bold;
    margin-bottom: 5px;
    padding-left: 5px;
    
}


.grid-step-container {
    display: grid;
    grid-template-columns: 0.4fr 2.4fr 2.3fr 0.5fr 0.2fr 0.2fr;
    background: white;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        ". . . . . ."
        ". . . . . ."
        ". . . . . .";
        margin: 0 7px 0 7px;
}

.img-addcircle{
    mask-position: center;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-image: var(--icon-path);
    aspect-ratio: 1/1;
    background: rgb(145, 226, 121);
    height: 25px;
    margin-top: 10px;
}

.img-dismisscircle{
    mask-position: center;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-image: var(--icon-path);
    aspect-ratio: 1/1;
    background: rgb(253, 31, 31);
    height: 25px;
    margin-top: 10px;
}

.grid-step-container p {
    border-bottom: 1px solid #f1f1f1;
    padding-bottom: 10px;
    padding-left: 7px;

}

.img-addCircle{
    background-color: #06387a;
} 

.flex-button {
    display: flex;
    justify-content: space-between;
    width: 340px;
    margin-left: 7px;
    margin-top: 15px;
}

.description-box{
    border: 1px solid #f1f1f1;
    display: flex;
    justify-content: space-between;
    margin: 20px 7px 20px 7px;
    padding: 10px 10px 10px 10px;
}

.ms-ComboBox-container{
    display: flex;
}

.relations-box{
    margin: 0 7px 10px 7px;
    height: 100px;
    background-color: white;
    border: 1px solid dimgray;
    border-top: 0px;
    padding-top: 0px;
}

.flex-relations{
    display: flex;
    margin-left: 7px;

}

.grid-version{
    display: grid;
    grid-template-columns: 0.1fr 0.9fr;
    background: white;
    grid-template-rows: 30px 30px;
    grid-auto-flow: row;
    grid-template-areas:
        ". ."
        ". .";
    margin: 0 7px 0 7px;
    border: 1px solid black;
    align-items: center;
}

.grid-version p{
    margin-left: 15px;
}

.attached-file{
    background: white;
    border: 1px solid black;
    margin: 10px 7px 0 7px;
    font-weight: bold;
}

.flex-file{
    display: flex;
    margin: 10px 7px 40px 7px;
    border: 1px solid #f1f1f1;
    height: 30px;
    padding-top: 7px;
}
