.sideNav-container{
    height: 450px; /* 100% Full-height */
    width: 252px; /* 0 width - change this with JavaScript */
    position: absolute;
    z-index: 1; /* Stay on top */
    top: 312px; /* Stay at the top */
    left: -230px;
    background-color: #d3d3d3; /* Black*/
    overflow-x: hidden; /* Disable horizontal scroll */
    transition: 0.4s ease-in-out; /* 0.5 second transition effect to slide in the sidenav */
    border-radius: 0 5px 5px 0;
}

.sideNav-container:hover{
    left:0px;
    
}

.title-container{
    background:#E34234;
    margin-top:-20px;
    color: white;
    
}

.title {
    font:"Segoe UI Variable", "Segoe UI", sans-serif;
    padding:30px 0 30px 0;
    text-align: center;
}