.projectbrowser-body{
    height: 100%;
}

.header-browser{
    display: flex;
    justify-content: flex-end;
    margin-right:10px;
}

.header-browser span{
    margin-right: 10px;
    font-family:"Segoe UI Variable", "Segoe UI", sans-serif;
}
.homepage-container{
    display: flex;
    flex-direction: row;
    background:#f1f1f1;
   /*  height: 1013px; */
    padding-top: 20px;
    position: relative;
}

.detail-container{
    display: flex;
    flex-direction: column;
    width: 74%;
    height: 1013px;
}
.combobox-container{
    display: flex;
    position: relative;
    justify-content: flex-start;
    margin: 10px 10px 10px 10px;
    font-family:"Segoe UI Variable", "Segoe UI", sans-serif;
}
.chart-homepage-container{
    background: white;
    width: auto;
    height:auto;
    overflow: hidden;
    display: flex;
}
.breakdown-gallery-container{
    height:100%;
    display: flex;
}

.flex-container-track-bds{
    display: flex;
    flex-direction: column;
    width: 90%;
    height: 100%;
}

.timeline-projectbrowser{
    width: 100%;
    height:35%;
    background: white;
    overflow-y: scroll;
    z-index: 50;
}


.flex-gallery-map{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    height: 100%;
}

.gallery{
    width: 470px;
    height: 370px;
}

.map{
    width: 470px;
    height:200px;
    background:#f1f1f1;
}

.graph-relations{
    width: 470px;
    height:400px;
    background:#f1f1f1;
    cursor: pointer;
    position: relative;
}

.header-cels{
    color: white;
    background: hsl(207,100%,30%);
    margin-bottom: 5px;
    padding-left: 5px;
}

.header-cels p{
    font-family:"Segoe UI Variable", "Segoe UI", sans-serif;
    margin: 0 0 0 0;
}

.filter-container{
    height: 200px;
}

.header-filter{
    background:  linear-gradient(0deg,#81b0ee, #d5e4f5);
    font-family:"Segoe UI Variable", "Segoe UI", sans-serif;
    color: hsl(207,100%,30%);
    font-weight: bold;
    margin-bottom: 5px;
    padding-left: 5px;
}

.filter-combobox{
    background:#EEEEEE;
    border: 1px solid black;
    width: 98%;
    height: auto;
    
}
.container-tree-view{
background: white;
width: 500px !important;
height: 100%;
overflow-y: scroll;
margin-right: 5px;
}

.container-tree-view [name=icon]{
    background: rgb(255,202,40) !important;
}

.treemapchart-container{
    width: 550px;
    height: 550px;
    box-shadow: 0px 10px 15px -3px rgba(0,0,0,0.1);
    border-radius: 10px;
    background: white;
}

.testcase-container{
    width: 100%;
    background:#d5e4f5;
    margin-bottom:5px;
    height: 98%;
    overflow-y: scroll;
}
