.homepage-container{
    display: flex;
    flex-direction: row;
    background:#f1f1f1;
    height:800px; 
    padding-top: 100px;
    position: relative;
}

.filter-button-timeline{
  display: flex;
  justify-content:space-evenly;
  width: 500px;
  margin-left: 32px;
  margin-bottom: 20px;
}
.timeline{
  overflow: scroll;
  background: white;
  height: 500px;
  margin-top: 10px;
  padding-top: 20px;
  box-shadow: 0px 10px 15px -3px rgba(0,0,0,0.1);
}
.timeline h4{
  margin-left: 50px;
}

.display-view-container{
  display: flex;
  flex-direction: column;
  margin-left: -80px;
}
.chart-homepage-container{
    background: white;
    width: auto;
    height: 500px;
    overflow: hidden;
    display: flex;
}

.directory{
background: white;
width: 300px;
height: 413px;
margin-right:100px;
overflow: scroll;
}

.treemapchart-container{
    width: 550px;
    height: 550px;
    box-shadow: 0px 10px 15px -3px rgba(0,0,0,0.1);
    border-radius: 10px;
    background: white;
}

.level-abstraction{
    margin-left: 50px;
}

#circle-mega{
    position: relative;
    background:#FF7F50;
    border-radius:50%;
    width:160px;
    height:160px;
    border:1px solid #dbdbdb;
    text-align: center;
    color: white;
    cursor: pointer;
  }
  #circle-macro{
    position: absolute;
    background:#4169e1;
    border-radius:50%;
    width:135px;
    height:135px;
    border:1px solid #dbdbdb;
    text-align: center;
    color: white;
    left:10px;
    bottom:0;
    box-shadow: 10px 10px 15px -3px rgba(0,0,0,0.1);
  }

  #circle-meso{
    position: absolute;
    background:#E34234;
    border-radius:50%;
    width:110px;
    height:110px;
    border:1px solid #dbdbdb;
    text-align: center;
    color: white;
    left:22px;
    bottom:0;
  }

  #circle-micro{
    position: absolute;
    background:#ADD8E6;
    border-radius:50%;
    width:85px;
    height:85px;
    border:1px solid #dbdbdb;
    text-align: center;
    color: white;
    left:11px;
    bottom:0;
  }

  #circle-nano{
    position: absolute;
    background:#00C0A3;
    border-radius:50%;
    width:60px;
    height:60px;
    border:1px solid #dbdbdb;
    text-align: center;
    color: white;
    left:11px;
    bottom:0;
  }