.grid-app-container{
    display: grid;
    width:200px;
    grid-template-columns: 1fr 1fr;
    gap: 10px 40px;
    margin-left: 20px;
}

.app-box{
    display: grid;
    grid-template-columns: 0.1fr 0.9fr;

}

.app-box span{
    font-size:14px ;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif ;
    margin-left: 10px;
    margin-left: 10px;
    margin-top:auto;
    margin-bottom: auto;
}