.container-grid-homepage {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 0.1fr 2fr;
    gap: 0px 0px;
    grid-template-areas:
        "header"
        "finderDetail";
    /* text-align: -webkit-right; */
    background-color: #f1f1f1;
}

.testexecution-details-container{
    height: 1000px;
    overflow-y: scroll;
    padding-right: 10px;
}

.header {
    grid-area: header;
}

.header__block{
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    gap: 0 5px;
    width: 440px;
    
}
.finder-details-container{
    grid-area: finderDetail;

}

.header-title {
    background: linear-gradient(0deg, #81b0ee, #d5e4f5);
    font-family: "Segoe UI Variable", "Segoe UI", sans-serif;
    color: hsl(207, 100%, 30%);
    font-weight: bold;
    margin-bottom: 5px;
    padding-left: 5px;
    
}

.header-cels{
    color: white;
    background: hsl(207,100%,30%);
    margin-bottom: 5px;
    padding-left: 5px;
}

.filter-combobox-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px 40px 10px 5px;
    gap: 10px;
    border: 1px solid black;
    width: 90%;
}

.header-cels p{
    font-family:"Segoe UI Variable", "Segoe UI", sans-serif;
}

.container-tree-view [name=icon]{
    background: rgb(255,202,40) !important;
}

.last-execution-box{
    width: 100%;
    background: white;
    height: 120px;
    border-radius: 10px;
    margin: 0 5px 0 5px;

}

.last-execution-box p{
    font-size: 18px;
}

.running-box{
    width:98%;
    background-color: black;
    height: 30px;
    margin: 0 15px 0 15px;
    text-align: center;
    padding-top: 7px;
    color: white;
    font-size: 20px;
}

.test-description-box{
    width: 100%;
    background: hsl(207,100%,30%);
    height: 70px;
    border-radius: 10px;
    margin: 0 5px 5px 5px;
    color: white;
}

.test-description-box p{
    font-size: 18px;
}

.test-execution-box{
    background: rgb(255,255,204);
    width: 98%;
    height: auto;
    margin: 0 5px 0 5px;
    border-radius: 10px;
    padding-top: 20px;
    padding:20px 20px 20px 20px;
}

.test-execution{
    background: #f1f1f1;
    width: 100%;
    height: 100%;
    border: 1px solid black;
}

.grid-title-step{
    display: grid;
    grid-template-columns: 0.3fr 1.2fr 1.8fr 0.5fr 1.7fr 0.5fr;
}

.grid-step-action-container{
    display: grid;
    grid-template-columns: 0.3fr 1.2fr 1.8fr 0.5fr 1.7fr 0.5fr;
    grid-template-rows: min-content min-content 1fr min-content;
    gap: 20px 5px;
    grid-auto-flow: row;
    grid-template-areas:
    ". . . . . ."
    ". . . . . ."
    "select-file select-file select-file select-file select-file select-file";
    margin: 0 7px 0 7px;
}
  .select-file { 
    grid-area: select-file; 
    background: rgb(204,221,238);
    padding: 3px 3px 3px 3px;
    position: relative;

}

.select-file::after{
    content: "";
    height: 1px;
    width: 100%;
    background: rgb(127, 127, 127);
    position: absolute;
    top: 47px;
    left: 0;

}

.select-file span{
    margin-left: 5px;
    margin-right: 5px;
}

.note-description-box{
    text-align: center;
    padding-left: 5px;
    margin-bottom: 20px;
}

.conclusion-box{
    width: 250px;
    height: 250px;
    background-color: white;
    border: 1px solid rgb(127, 127, 127);
    padding-left: 10px;
}

.grid-note-desc-box{
    display: grid;
    grid-template-columns: 80% 20%;
    gap: 20px;
    width: 100%;
    text-align: left;
}
