.req-meta{
    height: auto;
    background-color: white;
    border: 1px solid black;
    margin: 10px 7px 0 7px;
}

.box-scope-coverage{
    position: relative;
    margin: 15px 7px 0 7px;
    height: 50px;
    background-color: white;
    border: 1px solid black;
}

.scope-coverage-title{
    position: absolute;
    top:-25px;
    left: 10px;
    background-color: white;
    font-weight: bolder;
    color: hsl(207, 100%, 30%);
    font-size: 15px;
}