.popup {
    position: absolute;
    margin: auto;
    background-color: #f1f1f1;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    overflow-y: scroll;
    transition: all 0.4s ease-in-out;
    width: 100%;
    height: 100%;
}
