.dbsChart-container{
    display: flex;
    justify-content: space-between;
    margin-top:60px
}


.tag-container{
    list-style-type: none;
}

.tag-container span{
    position:absolute;
    background:#E34234;
    color: white;
    border-radius: 50%;
    padding: 3px 3px 3px 3px;
    right: 25px;
}

ul.tag-container {
padding-left: 15px;
font:"Segoe UI Variable", "Segoe UI", sans-serif;
}

.tag-container li{
    cursor:pointer;
}

.donetChart-container{
    width:250px;
    height:220px;
    margin-right: 20px;
    margin-left:30px;
    padding: 10px 10px 10px 10px;
    background: white;
    border-radius:10px;
}

.view-button-container{
    position: absolute;
    right: 0px;
}
.view-button-container img{
    cursor: pointer;
    margin-left:5px;
    width: 25px;
    background-color: #f1f1f1;
}

.treeview-container{
    margin-top:5px;
    height: 450px;
    width: 280px;
    overflow-y: scroll;
    background: white;
    overflow-x: hidden;
}