.myDiagramDiv {
  width: 1200px;
  height: 1000px;
  border: solid 1px black;
  background-color: white;
}

.diagram-component {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.diagram-finiteStateMachine {
  width: 1200px;
  height: 700px;
  border: solid 1px black;
  background-color: white;
  transition: all 0.4s ease-in-out;
}

.workflow-diagram-container {
  position: relative;
  margin-bottom: 30;
  width: 100%;
  height: 100%;
}

.button-span-container {
  position: absolute;
  z-index: 50;
  bottom: 0;
  right: 0;
}

.img-legend {
  position: absolute;
  z-index: 40;
  width: 750px;
  margin-top: 0;
}